// src/app/Header.js
import React from "react";
import "./styles/header.css";

const Header = () => {
    return (
        <header className="header">
            <div className="logo">Mupcop</div>
            <nav>
                <ul>
                    <li>Air quality</li>
                    <li>History data</li>
                </ul>
            </nav>
        </header>
    );
};

export default Header;